<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-button type="primary" @click="showAddCateDialog">添加分类</el-button>
      <!-- 表格 -->
      <el-table
        :data="catelist"
        row-key="cat_id"
        :tree-props="{ children: 'children' }"
        border
        :header-cell-style="{ background: '#FAFAFA' }"
      >
        <!-- 模拟索引列，空type 可以把tree的展开符移到下一列 -->
        <el-table-column label="#" type="" prop="index" width="48px"></el-table-column>
        <el-table-column label="分类名称" prop="cat_name"></el-table-column>
        <el-table-column label="是否有效" prop="cat_deleted">
          <template v-slot="scope">
            <i
              v-if="scope.row.cat_deleted == false"
              class="el-icon-success"
              style="font-size: 20px; color: lightgreen"
            ></i>
            <i v-else class="el-icon-error" style="font-size: 20px; color: red"></i>
          </template>
        </el-table-column>
        <el-table-column label="排序">
          <template v-slot="scope">
            <el-tag v-if="scope.row.cat_level == 0">一级</el-tag>
            <el-tag v-else-if="scope.row.cat_level == 1" type="success">二级</el-tag>
            <el-tag v-else type="warning">三级</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size.sync="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!-- 添加分类对话框 -->
      <el-dialog
        title="添加分类"
        :visible.sync="addCateDialogVisible"
        width="50%"
        @close="addCateDialogClosed"
      >
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="父级分类">
            <el-cascader
              v-model="selectedKeys"
              :options="parentCateList"
              :props="{
                expandTrigger: 'hover',
                value: 'cat_id',
                label: 'cat_name',
                children: 'children',
                checkStrictly: true
              }"
              @change="parentCateChange"
              clearable
            ></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addCate">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { getCateList, addCate } from '@/api/api.js'
export default {
  data() {
    return {
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 商品分类列表
      catelist: [],
      // 数据条数
      total: 0,
      inexd: 0,
      // 控制对话框显示隐藏
      addCateDialogVisible: false,
      // 添加分类的表单数据
      addCateForm: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      // 父级分类列表
      parentCateList: [],
      // 选中父级分类的id数组
      selectedKeys: [],
      // 表单验证
      addCateFormRules: {
        cat_name: [{ required: true, message: '请输入分类名称', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getCateList()
  },

  methods: {
    // 商品分类列表
    async getCateList() {
      const { data: res } = await getCateList(this.queryInfo)
      if (res.meta.status !== 200) return this.$message.error('获取商品分类列表失败')
      // 给第一级节点添加index，方便设置索引列
      // 因为组件默认的索引列会给所有列添加索引，而想要的效果是只需给一级节点添加索引
      let i = 1
      res.data.result.map(item => {
        return (item.index = i++)
      })
      this.catelist = res.data.result
      this.total = res.data.total
    },
    handleSizeChange() {
      this.getCateList()
    },
    handleCurrentChange() {
      this.getCateList()
    },
    // 展示添加分类对话框
    async showAddCateDialog() {
      // 获取父级分类（即前两级）
      const { data: res } = await getCateList({ type: 2 })
      if (res.meta.status !== 200) return this.$message.error('获取父级分类失败')
      this.parentCateList = res.data
      this.addCateDialogVisible = true
    },
    // 选中项发生变化触发
    parentCateChange() {
      // selectedKeys为选中项id
      // 根据选中项selectedKeys的长度判断选中了第几级
      // 数组有值时有两种情况：[一级id]或[一级id,二级id]
      if (this.selectedKeys.length > 0) {
        // 父级分类id，始终取选项数组最后一个id
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 当前要添加的分类的等级
        this.addCateForm.cat_level = this.selectedKeys.length
        return
      }
    },
    // 对话框关闭重置input和其他参数，及选中项
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
      this.selectedKeys = []
    },
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await addCate(this.addCateForm)
        if (res.meta.status !== 201) return this.$message.error('添加分类失败')
        this.$message.success('添加分类成功')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
