<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item to="/home">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-alert title="注意：只允许第三级分类设置相关参数" type="warning" show-icon :close="false"></el-alert>
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类：</span>
          <el-cascader
            v-model="selectedCateKeys"
            :options="catelist"
            :props="{ expandTrigger: 'hover', value: 'cat_id', label: 'cat_name', children: 'children' }"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- 标签页 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="动态参数" name="many">
          <el-button type="primary" size="mini" :disabled="selectedCateKeys.length == 3 ? false : true">
            添加参数
          </el-button>
          <!-- 表格 -->
          <el-table :data="manyTableData" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template v-slot="scope">
                <!-- 循环渲染tag -->
                <el-tag
                  v-for="(item, index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleClose(index, scope.row)"
                >
                  {{ item }}
                </el-tag>
                <!-- 切换显示input、button实现 new tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model.trim="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">
                  + New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="参数名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                >
                  编辑
                </el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                >
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="selectedCateKeys.length == 3 ? false : true"
            @click="addDialogVisible = true"
          >
            添加属性
          </el-button>
          <!-- 表格 -->
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开列 -->
            <el-table-column type="expand">
              <template v-slot="scope">
                <!-- 循环渲染tag -->
                <el-tag
                  v-for="(item, index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  @close="handleClose(index, scope.row)"
                >
                  {{ item }}
                </el-tag>
                <!-- 切换显示input、button实现 new tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model.trim="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">
                  + New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="静态属性" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                >
                  编辑
                </el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                >
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加 参数&属性 的对话框 -->
    <el-dialog
      :title="activeName == 'many' ? '添加参数' : '添加属性'"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <el-form :model="addForm" :rules="FormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="activeName == 'many' ? '动态参数' : '静态属性'" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = true">取 消</el-button>
        <el-button type="primary" @click="addParams">确 认</el-button>
      </span>
    </el-dialog>
    <!-- 编辑 参数&属性 的对话框 -->
    <el-dialog
      :title="activeName == 'many' ? '编辑参数' : '编辑属性'"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClose"
    >
      <el-form :model="editForm" :rules="FormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="activeName == 'many' ? '动态参数' : '静态属性'" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams">确 认</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getCateList, getParamsList, addParams, getParams, editParams, removeParams } from '@/api/api.js'
export default {
  data() {
    return {
      // 商品分类列表
      catelist: [],
      // 级联选择器选中项
      selectedCateKeys: [],
      // 三级分类的id
      cateId: '',
      // 标签页选中项
      activeName: 'many',
      // 动态参数数据
      manyTableData: [],
      // 静态属性数据
      onlyTableData: [],
      // 控制添加对话框的显示隐藏
      addDialogVisible: false,
      // 添加参数&属性的表单
      addForm: {},
      // 控制编辑对话框的显示隐藏
      editDialogVisible: false,
      // 编辑参数&属性的表单
      editForm: {},
      // 添加表单的校验规则
      FormRules: {
        attr_name: [{ required: true, message: '请输入参数名称', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getCateList()
  },

  methods: {
    async getCateList() {
      const { data: res } = await getCateList()
      if (res.meta.status !== 200) return this.$message.error('获取商品分类列表失败')
      this.catelist = res.data
    },
    // 级联选择器选中项改变
    async handleChange() {
      this.getParamsList()
    },
    // 标签页选择项改变
    handleTabClick() {
      this.getParamsList()
    },
    // 获取参数列表
    async getParamsList() {
      // 选择范围，只允许选中三级分类。选中非三级分类，将选中项清空
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        // 清空tab的数据，否者选中三级分类后再去选二级分类，三级分类的tab数据依然会渲染到页面
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // 获取三级分类的id
      this.cateId = this.selectedCateKeys[2]
      const { data: res } = await getParamsList(this.cateId, this.activeName)
      if (res.meta.status !== 200) return this.$message.error('获取参数列表失败')
      res.data.forEach(item => {
        // 如果attr_vals为空字符，直接split会得[""]，会导致页面渲染出一个空tag
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 添加控制newTag功能中input显示隐藏的属性，和input的值
        item.inputVisible = false
        item.inputValue = ''
      })

      if (this.activeName == 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 添加对话框关闭
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    // 点击确认，添加参数
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await addParams(this.cateId, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) return this.$message.error('添加参数失败')
        this.$message.success('添加参数成功')
        this.addDialogVisible = false
        this.getParamsList()
      })
    },
    // 展示编辑对话框
    async showEditDialog(attr_id) {
      const { data: res } = await getParams(this.cateId, attr_id, this.activeName)
      if (res.meta.status !== 200) return this.$message.error('获取参数失败')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 编辑对话框关闭
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 点击确认，提交编辑
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await editParams(
          this.cateId,
          this.editForm.attr_id,
          this.editForm.attr_name,
          this.activeName
        )
        if (res.meta.status !== 200) return this.$message.error('修改参数失败')
        this.$message.success('修改参数成功')
        this.editDialogVisible = false
        this.getParamsList()
      })
    },
    // 删除参数项
    async removeParams(attr_id) {
      const confirmResult = await this.$confirm('是否删除该参数？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return
      const { data: res } = await removeParams(this.cateId, attr_id)
      if (res.meta.status !== 200) return this.$message.error('删除参数失败')
      this.$message.success('删除参数成功')
      this.getParamsList()
    },
    // 点击 +New tag，展示input，并在input渲染后自动获取焦点
    showInput(row) {
      row.inputVisible = true
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    async saveAttrVals(row) {
      const { data: res } = await editParams(
        this.cateId,
        row.attr_id,
        row.attr_name,
        this.activeName,
        row.attr_vals.join(' ')
      )
      if (res.meta.status !== 200) return this.$message.error('更新参数失败')
      this.$message.success('更新参数成功')
    },
    // 失去焦点或按下Enter，添加newTag
    handleInputConfirm(row) {
      if (row.inputValue.length == 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      row.attr_vals.push(row.inputValue)
      row.inputValue = ''
      row.inputVisible = false
      this.saveAttrVals(row)
    },
    // 根据索引删除tag
    handleClose(index, row) {
      row.attr_vals.splice(index, 1)
      this.saveAttrVals(row)
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 5px;
}
.input-new-tag {
  width: 100px;
}
</style>
